#loadingIndicator {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  animation: fade infinite 1s cubic-bezier(0.075, 0.82, 0.165, 1);
}

@keyframes fade {
  0%,
  100% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}

.vendorWrapper {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  /* background-color: #64adc5; */
  overflow: hidden;
}

.playerPreloader {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  /* background-color: #64adc5; */
}

.playerPreloadCircle {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 10px solid rgba(255, 255, 255, 0.5);
  border-top: 10px solid #ffffff;
  box-sizing: border-box;
  animation: preloadspin 1s linear infinite;
}

@keyframes preloadspin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

#landscapeMode {
  position: fixed;
  top: 50%;
  color: #fff;
  width: 100%;
  z-index: 1000;
  animation: fade infinite 1s cubic-bezier(0.075, 0.82, 0.165, 1);
  text-align: center;
}
